import React, { Component }from "react";
export default class Login extends Component {

    constructor(props){
        super(props);
        this.state={username:'',password:''}
    }

    handleChange(e) {
        this.setState({ [e.target.name] : e.target.value });
     }

    render() {
        return (
            <div>
                <div className="form-group">
                <input type="email" className="form-control" name="username" value={this.state.username} placeholder="Enter email" onChange={this.handleChange.bind(this)}/>
                </div>

                <div className="form-group">
                    <input type="password" className="form-control" name="password" value={this.state.password}  placeholder="Enter password" onChange={this.handleChange.bind(this)} />
                </div>

                <button type="submit" className="btn btn-primary btn-block" onClick={(event) => this.handleLogin(event)}>Login</button>
            </div>
        );
    }

    handleLogin(event){
        console.log(this.state.username);
        console.log(this.state.password);

        //make api call to check if this true.
        //on call back, set some value to true, and display a create div
    }
}